	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/09/crhwatersand.jpg\" height=\"325\" width=\"244\" border=\"0\" alt=\"crhwatersand.jpg: \" /></p>\n<p>In a milestone that only a parent could appreciate, Christopher barked like a dog for the first time today. He was answering his Barney computer, which makes various animal and machine noises when he pushes the picture buttons. \"Woof woof, dog, woof woof\", says Barney. \"Woof woof\", answers Christopher. \"Woof woof\", chime in Mom and Dad in the background.</p>\n<p><a href=\"http://moorbek.weblogs.com/2001/09/15\">Moorbek</a> asks whether the English word \"war\" is not an exact translation of the the German word \"Krieg\", since the US seems to be speaking of \"war\" more lightly than a German would of \"Krieg\". I guess a \"war\" takes place far away, while a \"Krieg\" destroys everything at home.</p>","fields":{"title":"World in a Stupor","date":"17 Sep 2001","url":"https://papascott.de/2001-09-17-world-in-a-stupor/"}}},"pathContext":{"slug":"/2001-09-17-world-in-a-stupor/","prev":{"fields":{"title":"Sunday at Home","date":"16 Sep 2001","slug":"/2001-09-16-sunday-at-home/"}},"next":{"fields":{"title":"Move Along","date":"18 Sep 2001","slug":"/2001-09-18-move-along/"}}}}

